<script>
import LoadingIndicator from "@/components/LoadingIndicator.vue";
import MultiImageViewer from "@/components/Multi-Image-Viewer.vue";

// Services
import ProductService from "@/services/products.js";
import CategoryService from "@/services/category.js";

export default {
  name: "Inventory Product",
  components: {
    LoadingIndicator,
    MultiImageViewer,
  },
  data() {
    return {
      product: {},
      category: {},
      display: false,
      loading: false,
      load_error: "",
    };
  },
  methods: {
    // TODO: localize
    getUnit(index) {
      switch (index) {
        case 0:
          return "units";
        case 1:
          return "kilos";
        case 2:
          return "liters";
      }
    },
    DateFormat(datestring) {
      const date = new Date(Date.parse(datestring));
      return date;
    },
    deleteProduct() {
      ProductService.deleteProduct(this.product.id).then(
        (response) => {
          this.$router.push("/inventory");
        },
        (error) => {
          this.load_error =
            (error.response &&
              error.response.data &&
              error.response.data.message) ||
            error.message ||
            error.toString();
        }
      );
    },
    editProduct() {
      this.$router.push("/inventory/update/" + this.product.id);
    },
    // getImage() {
    // 	if (this.product.img && this.product.img[0] != "img") {
    // 		// Check if image is active
    // 		// TODO: check image active
    // 		// const img = fetch(this.product.img[0]);
    // 		// console.log(img);
    // 		return this.product.img[0];
    // 	} else {
    // 		return require("@/assets/images/product.png");
    // 	}
    // },
  },
  mounted() {
    // Set Loading indicator to running
    // Empty error text
    this.loading = true;
    this.load_error = "";

    ProductService.getSingleProduct(this.$route.params.id).then(
      (response) => {
        this.loading = false;
        this.display = true;
        this.product = response.data.data;
        console.log("PRODUCT:", response);
        CategoryService.getSingleCategory(this.product.category_id).then(
          (response) => {
            this.category = response.data.data.category;
          },
          (error) => {
            console.log(error);
          }
        );
      },
      (error) => {
        this.loading = false;
        console.log("Error:", error);
        this.load_error =
          (error.response &&
            error.response.data &&
            error.response.data.message) ||
          error.message ||
          error.toString();
      }
    );
  },
};
</script>